<template>
   <div class="header" >
      <!-- ICON TOP LEFT -->
        <div class="icon">
            <div class="w-12">
                <router-link to="/login"  class="btn bg-green-light">
                 <template v-if="isAuthenticated" id="userIdentityName">          
                      {{abbreviations}}
                    </template>
                    <template v-else>
                      <icon name="user"/>
                    </template>
                     
                </router-link>
            </div>
        </div>
        <div class="body">
            <div class="flex-no-grow" style="margin-top:5px">
                <app-number :title="'Play'" v-bind:value="game.numberPlay"/> 
            </div>
            <div class="flex-grow header-logo" style="margin-top:-5px">
                <img src="../../assets/Logo.png" class="logo" style="width:100%">  
            </div>
            <div class="flex-no-grow" style="margin-top:5px">
               <app-number :title="'Total'" v-bind:value="gameTotal"/> 
            </div>
        </div>
        
        <!-- ICON TOP RIGHT -->
        <div class="icon">        
            <router-link to="/" class="float-right btn bg-red-light">
                <icon name="times"></icon>
            </router-link>
        </div>
    </div>
</template>


<script>
import { mapGetters } from "vuex";
import { mapActions } from "vuex";
import AnimationNumber from "../Animations/Number";
export default {
   methods: {
    ...mapActions({
      login: "login",
      logout: "logout"
    })
  },
  computed: {
    ...mapGetters({
      isAuthenticated: "isAuthenticated",
      user: "user"
    }),
    abbreviations() {
      return this.$store.getters.user.displayName != null
        ? this.$store.getters.user.displayName.charAt(0).toUpperCase()
        : this.$store.getters.user.email.charAt(0).toUpperCase();
    },
    game() {
      return this.$store.getters.game;
    },
    gameLevel() {
      return this.$store.getters.gameLevel;
    },
    gameTotal() {
      return this.$store.getters.sumTilesTaken;
    },
  },
  components: {
    appNumber : AnimationNumber
  }
};
</script>


