<template>
  <div class="login" id="Game">
     <app-header/>
     <div class="game-body"
      v-bind:style="{ height: gameTileHeightSize  + 'px' }"
     >
        <div class="vertical-center">
          <div class="container mx-auto h-full flex justify-center items-center">
            <div >
                <!-- <h1 class="font-hairline mb-6 text-center">Login</h1> -->
                <div class="border-teal p-8 border-t-12 bg-white mb-6 rounded-lg shadow-lg">
                    <div class="mb-4">
                        <label class="font-bold text-grey-darker block mb-2">Email</label>
                        <input type="email" required  v-model="email"  class="block appearance-none w-full bg-white border border-grey-light hover:border-grey px-2 py-2 rounded shadow" placeholder="Your Email">
                    </div>

                    <div class="mb-4">
                        <label class="font-bold text-grey-darker block mb-2">Password</label>
                        <input type="password" required v-model="password" class="block appearance-none w-full bg-white border border-grey-light hover:border-grey px-2 py-2 rounded shadow" placeholder="Your Password">
                    </div>

                    <div class="flex items-center justify-between">
                        <button v-on:click="signIn" class="bg-teal-dark hover:bg-teal text-white font-bold py-2 px-4 rounded">
                            Login
                        </button>

                        <!-- <a class="no-underline inline-block align-baseline font-bold text-sm text-blue hover:text-blue-dark float-right" href="#">
                            Forgot Password?
                        </a> -->
                    </div>
                    
                </div>
                <div class="text-center">
                    <p class="text-grey-dark text-sm">Don't have an account? 
                      <router-link class="no-underline text-blue font-bold" to="/sign-up">Create an Account</router-link>
                      </p>
                </div>
            </div>
          </div>
        </div>
     </div>
     

   
  </div>
</template>

<script>
import firebase from "firebase";
import Header from "../Game/Header";
export default {
  name: "login",
  data: function() {
    return {
      email: "",
      password: "",
      gameTileHeightSize:0
    };
  },
  methods: {
    handleWindowResize: function() {
      this.gameTileHeightSize =
          Math.round(
            (_.toSafeInteger(document.querySelector(".AppHeight")?document.querySelector(".AppHeight").scrollHeight:0) -
              (_.toSafeInteger(document.querySelector(".header")?document.querySelector(".header").clientHeight:0) 
                + _.toSafeInteger(document.querySelector(".footer")? document.querySelector(".footer").clientHeight : 0) 
                + _.toSafeInteger(document.querySelector(".dice")?document.querySelector(".dice").clientHeight:0)
                )));
    },
    signIn: function() {
      // debugger;
      let self = this;

      firebase
        .auth()
        .signInWithEmailAndPassword(this.email, this.password)
        .then(
          user => {
            self.$toasted.success("Logged on as " + self.email);
            self.$router.replace("game");
          },
          err => {
            self.$toasted.error(err.message);
          }
        )
        .catch(err => {
          self.$toasted.error(err.message);
        })
        .then(() => {
          //NProgress.done();
        });
    }
  },
  created() {
    //NProgress.start();
    //this.$nprogress.start();
  },
  beforeDestroy: function() {
    window.removeEventListener("resize", _.debounce(this.handleWindowResize, 500));
  },
  mounted() {
    window.addEventListener("resize", _.debounce(this.handleWindowResize, 500));
    this.handleWindowResize()
  },
  components: {
    appHeader: Header
  }
};
</script>



