<template>
  <div class="sign-up">
 <app-header/>
     <div class="game-body"
      v-bind:style="{ height: gameTileHeightSize  + 'px' }"
     >
        <div class="vertical-center">
    <div
    
     class="container mx-auto h-full w-full flex justify-center items-center">

      

       <form autocomplete="off" @submit.prevent="validateForm('form-2')"  data-vv-scope="form-2">
        <div >
            <!-- <h1 class="font-hairline mb-6 text-center">Login</h1> -->
            <div style="width: 50vh!important;" class="border-teal p-8 border-t-12 bg-white mb-6 rounded-lg shadow-lg">
              <div class="mb-4">
                    <label class="font-bold text-grey-darker block mb-2">Username</label>
                    <input 
                    autocomplete="off"
                    name="username"
                     v-validate="'required|min:4'" 
                     :class="{'input': true, 'is-danger': 
                    errors.has('form-2.username') }"
                    data-vv-delay="1000"
                    type="text" 

                    readonly 
                    onfocus="if (this.hasAttribute('readonly')) { this.removeAttribute('readonly');  this.blur();    this.focus();  }"
                      v-model="username"  
                      class="block appearance-none w-full bg-white 
                      border border-grey-light hover:border-grey px-2 py-2 
                      rounded shadow" 
                      placeholder="Your Username">
                 <span v-show="errors.has('form-2.username')" 
                     class="help is-danger">{{ errors.first('form-2.username') }}</span>
                </div>

                <div class="mb-4">
                    <label class="font-bold text-grey-darker block mb-2">Email</label>
                    <input
                    name="email"
                    v-validate="'required|email'" 
                    :class="{'input': true, 'is-danger': 
                    errors.has('form-2.email') }"
                    data-vv-delay="1000"
                    readonly 
                    onfocus="if (this.hasAttribute('readonly')) { this.removeAttribute('readonly');  this.blur();    this.focus();  }"
                    
                     autocomplete="off" type="email" 
                      v-model="email"  
                      class="block appearance-none w-full bg-white 
                      border border-grey-light hover:border-grey px-2
                       py-2 rounded shadow" 
                       placeholder="Your Email Address">
                     <span v-show="errors.has('form-2.email')" 
                     class="help is-danger">{{ errors.first('form-2.email') }}</span>
                </div>

                <div class="mb-4">
                    <label class="font-bold text-grey-darker block mb-2">Password</label>
                    <input autocomplete="off" type="password"
                    name="password"
                    readonly 
                    onfocus="if (this.hasAttribute('readonly')) { this.removeAttribute('readonly');  this.blur();    this.focus();  }"
                    
                     v-validate="'required|min:4'" 
                     :class="{'input': true, 'is-danger': 
                    errors.has('form-2.password') }"
                     v-model="password" class="block appearance-none w-full bg-white border border-grey-light hover:border-grey px-2 py-2 rounded shadow" placeholder="Your Password">
                <span v-show="errors.has('form-2.password')" 
                     class="help is-danger">{{ errors.first('form-2.password') }}</span>
                </div>

                <div class="flex items-center justify-between">
                  <!-- v-on:click="signUp"  -->
                    <button
                     
                     type="submit"
                     class="bg-teal-dark hover:bg-teal text-white font-bold py-2 px-4 rounded">
                        Sign Up
                    </button>

                    <!-- <a class="no-underline inline-block align-baseline font-bold text-sm text-blue hover:text-blue-dark float-right" href="#">
                        Forgot Password?
                    </a> -->
                </div>
                
            </div>
            <div class="text-center">
                <p class="text-grey-dark text-sm">
                   <router-link class="no-underline text-blue font-bold" to="/login">Go back to Login</router-link>
                  </p>
            </div>
        </div>
       </form>
    </div>
   </div>
   </div>
  </div>
</template>

<script>
import firebase from "firebase";
import Header from "../Game/Header";
export default {
  name: "signUp",
  data: function() {
    return {
      email: "",
      password: "",
      username: "",
      gameTileHeightSize: 0
    };
  },
  methods: {
    handleWindowResize: function() {
      this.gameTileHeightSize = Math.round(
        _.toSafeInteger(
          document.querySelector(".AppHeight")
            ? document.querySelector(".AppHeight").scrollHeight
            : 0
        ) -
          (_.toSafeInteger(
            document.querySelector(".header")
              ? document.querySelector(".header").clientHeight
              : 0
          ) +
            _.toSafeInteger(
              document.querySelector(".footer")
                ? document.querySelector(".footer").clientHeight
                : 0
            ) +
            _.toSafeInteger(
              document.querySelector(".dice")
                ? document.querySelector(".dice").clientHeight
                : 0
            ))
      );
    },
    validateForm(scope) {
      this.$validator.validateAll(scope).then((result) => {
        if (result) {
          // eslint-disable-next-line
          alert('Form Submitted!');
        }
      });
    },
    signUp: function() {
      let self = this;
      firebase
        .auth()
        .createUserWithEmailAndPassword(this.email, this.password)
        .then(
          user => {
            debugger;
            self.$toasted.success("Logged on as " + self.email);

            var u = firebase.auth().currentUser;

            u.updateProfile({
                displayName: self.username
              })
              .then(() => {
                self.$toasted.success("Username created " + self.username);
                if (!u.emailVerified) {
                  u.sendEmailVerification().then(() => {
                    self.$toasted.success(
                      "Email validation sent to " + self.email
                    );
                  });
                }
                this.$router.replace("game");
              });
          },
          err => {
            self.$toasted.error("Oops.  " + err.message);
          }
        );
    }
  },
  beforeDestroy: function() {
    window.removeEventListener(
      "resize",
      _.debounce(this.handleWindowResize, 500)
    );
  },
  mounted() {
    window.addEventListener("resize", _.debounce(this.handleWindowResize, 500));
    this.handleWindowResize();
  },
  components: {
    appHeader: Header
  }
};
</script>


