<template>
  <div id="Game">
    
    <app-header/>
    <div class="game-body" 
     v-bind:style="{ height: gameTileHeightSize  + 'px' }"
    >
      <div class="vertical-center">
        <app-tiles v-for="(tiles, index) in tilesObj" 
        :index="index" 
        :allTiles="tilesObj" 
        :tiles="tiles" 
        :clickAudio="clickAudio" 
        :key="tiles.index"/>
      </div>
    </div>
    <app-dice/>
    <app-footer/>
  </div>
</template>
<script>
import "@/assets/styles/main.css";
import Header from "./Header"
import Footer from "./Footer"
import Dice from "./Dice"
import Tiles from "./Tiles"
export default {
  name: "Game",
  props: {
    size: {
      default: '1'
    },
    bool:{
      type: Boolean,
      default: false
    }
  },
  methods:{
    handleWindowResize: function() {
      this.gameTileHeightSize =
          Math.round(
            (_.toSafeInteger(document.querySelector(".AppHeight")?document.querySelector(".AppHeight").scrollHeight:0) -
              (_.toSafeInteger(document.querySelector(".header")?document.querySelector(".header").clientHeight:0) 
                + _.toSafeInteger(document.querySelector(".footer")? document.querySelector(".footer").clientHeight : 0) 
                + _.toSafeInteger(document.querySelector(".dice")?document.querySelector(".dice").clientHeight:0)
                ))); 
    }
  },
  data(){
    return {
      gameTileHeightSize:0,
    }
  },
  //props: ['size', 'bool'],
  computed:{
    
    tilesObj() {
      return this.$store.getters.tiles == null ? {} : this.$store.getters.tiles;
    }
  },
  components:{
      appHeader:Header,
      appFooter:Footer,
      appDice:Dice,
      appTiles:Tiles
  },
  created(){

    this.clickAudio  = new Audio(document.querySelector('#click').src);

    if (!this.$route.params.level){
      this.$router.push({ path: 'init' });
    }else{
      //debugger;
      let _size = this.size;
      let _bool = this.bool;
      switch (this.$route.params.level) {
        case 1:
        case "1":
          _size = 1;
          break;
        case 2:
        case "2":
          _size = 9;
          _bool = false;
          break;
        case 3:
        case "3":
          _size = 9;
          _bool = false;
          break;
        default:
          this.$router.push({
            name: "Init"
          });
          break;
      }
      this.$store.dispatch("initTiles", {
         size: _size,
         isRandom: _bool
       });
      this.$store.dispatch("restartGame");
      this.$store.commit("SET_GAME_IS_VISIBLE", true);
    }
  },
  beforeDestroy: function() {
    window.removeEventListener("resize", _.debounce(this.handleWindowResize, 500));
  },
  mounted() {
    window.addEventListener("resize", _.debounce(this.handleWindowResize, 500));
    this.handleWindowResize()
  }
};
</script>





