<template>
  <div class="subscribe">
    <div class="topheader">
        
    </div>
    <div class="body "  v-bind:style="{ height: gameTileHeightSize  + 'px' }">
        <div class="mb-8 logo" style="padding:0 10px 0 10px;">
            <img src="../../assets/Logo.png" style="max-width:200px;" > 
            <span class="version">v{{version}}</span>
        </div>
        <div class="vertical-align" style="padding:40px; align-text:justify;">
             
             <swiper :options="swiperOption">

                

        <swiper-slide>
            <div class="slider"
            v-bind:style="{ height: gameTileHeightSize - 120  + 'px' }"
            >
                    
                    <h2>Welcome to ShutTheBox App</h2>
                    <h3>What is Shut The Box?</h3>
                   <p> Shut The Box is a board game showing tiles from 1 to 9 and starts as open tiles, The game begins by rolling the dice and matching the dice result with the tiles available. If the sum of the remaining tile(s) is 6 or lower, the player may roll only one dice until you Shut The Box or Game Over.</p>
        
            </div>
             
        </swiper-slide>
         <swiper-slide>
            <div class="slider"
            v-bind:style="{ height: gameTileHeightSize - 120  + 'px' }"
            >
            <h2>Select a Game</h2>
            <h3>Each level has a different level of difficulty</h3>
            <ul>
                <li>
                    <div class="icon">
<img src="../../assets/simple_icon.gif"/>
                    </div>
                    <div class="content">
 Easy
                    <span>1 row, 9 tiles</span>
                    </div>
                    
                </li>
                <li>
                    <div class="icon">
<img src="../../assets/medium_icon.gif"/>
                    </div>
                    <div class="content">
 Medium
                    <span>9 row, 9 tiles, Pre-selected</span>
                    </div>
                    
                </li>
                <li>
                    <div class="icon">
<img src="../../assets/ninja_icon.gif"/>
                    </div>
                    <div class="content">
 Ninja
                    <span>9 row, 9 tiles</span>
                    </div>
                    
                </li>
                
            </ul>
            </div>
        </swiper-slide>
        <swiper-slide>
             <div class="slider"
            v-bind:style="{ height: gameTileHeightSize - 120  + 'px' }"
            >
            <h2>Roll the Dice</h2>
            <h3>Sum up the dice total</h3>
            <p>
                <img src="../../assets/roll_dice.gif"/>
            </p>
            </div>
            
            </swiper-slide>
        <swiper-slide>
             <div class="slider"
            v-bind:style="{ height: gameTileHeightSize - 120  + 'px' }"
            >
            <h2>Select Tiles</h2>
            <h3>Any tiles that sums up the rolled dice</h3>
            <p>
                <img src="../../assets/shutTheBox.gif"/>
            </p>
            </div>

        </swiper-slide>
        
                <div class="swiper-pagination" slot="pagination"></div>

      </swiper>
               
                    
                     </div>
    </div>
    <div class="iconFooter">
        <div>
      <!-- <button class="btn bg-green hover:bg-green-dark score" 
      @click="alert('work in progress')">
        <icon name="user-circle-o"></icon>
      Continue as Guest
     </button> -->


       <router-link to="/guest"  class="btn bg-green hover:bg-green-dark score" >
              <icon name="user-circle-o"></icon>
      Continue as Guest
                     
                </router-link>

    </div>

    <div>

          <router-link to="/guest"  class="btn bg-blue-dark hover:bg-blue-darkest" >
               <icon name="user-circle"></icon>
     Create an Account 
                     
                </router-link>

      <!-- <button class="btn bg-blue-dark hover:bg-blue-darkest " 
      @click="alert('work in progress')">
      <icon name="user-circle"></icon>
      Create an Account 
      </button> -->
    </div>
    </div>
    <div class="footer">

        <div class="flex-no-grow" style="margin-top:5px">
                <!-- <a class="btn bg-blue-light"  style="width:3rem !important" href="#">
                 <icon name="refresh"></icon>
             </a> -->
            </div>
            <div class="flex-grow header-logo" style="margin-top:-5px">
               
            </div>
            <div class="flex-no-grow" style="margin-top:5px; ">
                <!-- <a class="btn bg-blue-light " style="width:3rem !important" href="#">
                 <icon name="refresh"></icon>
             </a> -->
            </div>

        
    </div>
    
    

    

    
  </div>
</template>

<script>

import { version } from "../../../package.json";
export default {
  name: "app",
  data: function() {
    return {
      version: version,
      gameTileHeightSize:0,
      swiperOption: {
          pagination: {
            el: '.swiper-pagination'
          }
        }
    };
  },
 
  methods: {
    handleWindowResize: function() {
      this.gameTileHeightSize =
          Math.round(
            (_.toSafeInteger(document.querySelector(".AppHeight")?document.querySelector(".AppHeight").scrollHeight:0) -
              (_.toSafeInteger(document.querySelector(".topheader")?document.querySelector(".topheader").clientHeight:0) 
                + _.toSafeInteger(document.querySelector(".footer")? document.querySelector(".footer").clientHeight : 0) 
                + _.toSafeInteger(document.querySelector(".iconFooter")?document.querySelector(".iconFooter").clientHeight:0)
                ))); 
    }
  },
  beforeDestroy: function() {
    window.removeEventListener("resize", _.debounce(this.handleWindowResize, 500));
  },
  mounted() {
      window.addToHomescreen({
      maxDisplayCount: 1,
      displayPace: 0,
      skipFirstVisit: false
    });
    window.addEventListener("resize", _.debounce(this.handleWindowResize, 500));
    this.handleWindowResize()
  }
};
</script>

<style>
.slider{
    max-width: 300px;
    margin: 0 auto;
}
.slider ul {
    list-style-type: none;
    text-align: left;
    margin: 0;
    padding: 0;
}
.slider li{
    padding: 0 0 35px;    
    font-size: 14px;
}
.slider  .icon{
    float: left;
    width: 10%;
    padding-right: 10px;
    min-height: 24px;
}
.slider  .content{
    float: left;
    width: 85%;
   
    min-height: 24px;
    line-height: 24px;
}
.slider li span{
    font-size: 10px; color: #666;    
    float: right;
}
.slider h2 {
    font-size: 16px;
    padding-bottom: 5px;
}
.slider h3 {
    font-size: 12px;
    color: #666;
    padding-bottom: 20px;
}
.slider p{
    font-size: 12px;
}
</style>



